@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
:root {
  --Desaturated_Dark_Cyan: hsl(180, 29%, 50%);
  --Light_Grayish_Cyan_1: hsl(180, 52%, 96%);
  --Light_Grayish_Cyan_2: hsl(180, 31%, 95%);
  --Dark_Grayish_Cyan: hsl(180, 8%, 52%);
  --Very_Dark_Grayish_Cyan: hsl(180, 14%, 20%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: "Spartan", sans-serif;
  font-size: 15px;
}

.sec {
  position: relative;
  border-radius: 5px;
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  margin-top: 26px;
  padding: 30px 35px;
  width: 100%;
  min-height: 150px;
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.feated {
  border-radius: 5px 0 0 5px;
  background: hsl(180, 29%, 50%);
  background: var(--Desaturated_Dark_Cyan);
  left: 0;
  top: 0;
  bottom: 0;
  width: 6px;
  position: absolute;
}

.sec__left {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.sec__left img {
  border: none;
  border-radius: 100%;
  width: 90px;
  height: 90px;
  object-fit: cover;
  background: white;
}

.sec__left__content {
  width: 350px;
  height: 85px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-left: 25px;
}

.sec__left__content > h2 {
  margin-top: 2px;
  cursor: pointer;
  font-weight: 700;
  font-size: 1.2em;
  color: hsl(180, 14%, 20%);
  color: var(--Very_Dark_Grayish_Cyan);
  transition: 200ms ease;
}

.sec__left__content > div:first-child {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: baseline;
          align-items: baseline;
}

.sec__left__content > div:first-child h4 {
  font-weight: 700;
  color: hsl(180, 29%, 50%);
  color: var(--Desaturated_Dark_Cyan);
  font-size: 1em;
}

.sec__left__content > div:first-child p {
  text-transform: uppercase;
  border-radius: 50px;
  color: hsl(180, 31%, 95%);
  color: var(--Light_Grayish_Cyan_2);
  font-weight: 700;
  padding: 4px 8px 0 8px;
  font-size: 0.733em;
  letter-spacing: 0.5px;
  height: 25px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.new {
  margin-left: 20px;
  background: hsl(180, 29%, 50%);
  background: var(--Desaturated_Dark_Cyan);
}

.feature {
  background: hsl(180, 14%, 20%);
  background: var(--Very_Dark_Grayish_Cyan);
  margin-left: 9px;
}

.sec__left__content > div:last-child {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.sec__left__content > div:last-child p {
  font-weight: 500;
  font-size: 0.9333em;
  color: hsl(180, 8%, 52%);
  color: var(--Dark_Grayish_Cyan);
}

.sec__left__content > div:last-child span {
  display: block;
  border-radius: 100%;
  width: 4px;
  height: 4px;
  background: rgba(0, 0, 0, 0.296);
  margin: 0 18px 3px 18px;
}

.sec__right {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
}

.sec__right button {
  border-radius: 5px;
  border: none;
  cursor: pointer;
  background: hsl(180, 52%, 96%);
  background: var(--Light_Grayish_Cyan_1);
  color: hsl(180, 29%, 50%);
  color: var(--Desaturated_Dark_Cyan);
  font-weight: 700;
  font-size: 0.866em;
  padding: 3px 10px 0 10px;
  margin: 8px 0 8px 9px;
  height: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  transition: 200ms ease;
}

@media (hover: hover) {
  .sec__left__content > h2:hover {
    color: hsl(180, 29%, 50%);
    color: var(--Desaturated_Dark_Cyan);
  }
  .sec__right button:hover {
    background: hsl(180, 29%, 50%);
    background: var(--Desaturated_Dark_Cyan);
    color: hsl(180, 31%, 95%);
    color: var(--Light_Grayish_Cyan_2);
  }
}

@media screen and (max-width: 900px) {
  .sec {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
  .sec__left {
    padding-bottom: 20px;
  }
  .sec__right {
    border-top: 1px solid rgba(0, 0, 0, 0.2);
    width: 100%;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

  .sec__right button {
    margin: 16px 16px 0 0;
  }
}

@media screen and (max-width: 550px) {
  .sec {
    margin-top: 40px;
    padding: 30px 25px 20px 25px;
  }
  .sec__left {
    position: relative;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    padding-top: 15px;
  }

  .sec__right {
    padding: 10px 0;
  }

  .sec__left img {
    top: -55px;
    width: 50px;
    height: 50px;
    position: absolute;
  }

  .sec__left__content {
    height: 95px;
    width: -webkit-max-content;
    width: max-content;
    margin: 0;
  }

  .sec__left__content > div:first-child h4 {
    font-size: 1em;
  }

  .sec__left__content > div:last-child span {
    margin: 0 8px 3px 8px;
  }
}

:root {
  --Desaturated_Dark_Cyan: hsl(180, 29%, 50%);
  --Light_Grayish_Cyan_1: hsl(180, 52%, 96%);
  --Light_Grayish_Cyan_2: hsl(180, 31%, 95%);
  --Dark_Grayish_Cyan: hsl(180, 8%, 52%);
  --Very_Dark_Grayish_Cyan: hsl(180, 14%, 20%);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-family: "Spartan", sans-serif;
}

.body_background {
  width: 100%;
  min-height: 100vh;
  background: hsl(180, 52%, 96%);
  background: var(--Light_Grayish_Cyan_1);
}

header {
  position: relative;
  width: 100%;
  height: 155px;
  background: hsl(180, 29%, 50%);
  background: var(--Desaturated_Dark_Cyan);
}

header img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.filter_tags {
  width: 85%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.filter_tags > * {
  margin: 8px 14px 8px 0;
}

.filter_container {
  position: absolute;
  z-index: 5;
  top: 115px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 10px 35px;
  width: 90%;
  min-width: 325px;
  max-width: 1110px;
  min-height: 72px;
  border-radius: 5px;
  background: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.filter_container > button {
  border: none;
  background: transparent;
  position: relative;
  font-weight: 700;
  font-size: 0.875em;
  color: hsl(180, 29%, 50%);
  color: var(--Desaturated_Dark_Cyan);
  cursor: pointer;
}

.filter_container button span {
  display: block;
  width: 100%;
  height: 2px;
  opacity: 0;
  bottom: 0;
  transition: 200ms ease;
  position: absolute;
  background: hsl(180, 29%, 50%);
  background: var(--Desaturated_Dark_Cyan);
}

main {
  background: hsl(180, 52%, 96%);
  background: var(--Light_Grayish_Cyan_1);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

main > .joblist {
  min-width: 325px;
  max-width: 1110px;
  width: 90%;
  margin: 53px 0 120px 0;
  transition: 250ms ease;
}

.tag_container {
  position: relative;
  height: 32px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  background: hsl(180, 52%, 96%);
  background: var(--Light_Grayish_Cyan_1);
  border-radius: 5px;
  transition: 250ms ease;
}

.tag_container > p {
  margin-top: 3px;
  padding: 0 10px;
  font-weight: 700;
  font-size: 0.8125em;
  letter-spacing: 0.5px;
  color: hsl(180, 29%, 50%);
  color: var(--Desaturated_Dark_Cyan);
}

.close_btn {
  position: relative;
  border-radius: 0 5px 5px 0;
  width: 32px;
  height: 32px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: hsl(180, 29%, 50%);
  background: var(--Desaturated_Dark_Cyan);
  cursor: pointer;
  transition: 200ms ease;
}

.close_btn span {
  pointer-events: none;
  width: 18px;
  height: 3px;
  background: white;
}

.close_btn span:nth-child(1) {
  position: absolute;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.close_btn span:nth-child(2) {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

@media (hover: hover) {
  .close_btn:hover {
    background: hsl(180, 14%, 20%);
    background: var(--Very_Dark_Grayish_Cyan);
  }

  .filter_container button:hover > span {
    opacity: 1;
  }
}

@media screen and (max-width: 900px) {
  main > .joblist {
    margin: 15px 0 120px 0;
  }
  .filter_container {
    position: relative;
    top: auto;
    top: initial;
    margin-top: -30px;
  }
}

@media screen and (max-width: 550px) {
  .filter_container {
    padding: 10px 25px;
  }
}

